.typewriter {
    display: block;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    border-right: 0.15em solid #333;
    font-size: 60px;
    -webkit-animation: typing 1s steps(20, end), blink-caret 0.5s step-end infinite;
            animation: typing 1s steps(20, end), blink-caret 0.5s step-end infinite;
}

@-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

@-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #333 }
}

@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #333 }
}

.sentence {

    font-weight: bold;
    color: #333;
    opacity: 0;
    -webkit-animation: fade-in 0.5s ease-out forwards;
            animation: fade-in 0.5s ease-out forwards;
    margin-bottom: 10px;
    text-align: center;
}

@-webkit-keyframes fade-in {
    from { opacity: 0 }
    to { opacity: 1 }
}

@keyframes fade-in {
    from { opacity: 0 }
    to { opacity: 1 }
}



@-webkit-keyframes blink {
    50% {
      opacity: 0;
    }
  }



@keyframes blink {
    50% {
      opacity: 0;
    }
  }

  .blink {
    -webkit-animation: blink 1s linear infinite;
            animation: blink 1s linear infinite;
  }

